.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 250px;
  background-color: #f5f0eb;
  color: #4a3f35;
  transition: transform 0.3s ease;
  transform: translateX(-100%);
  padding: 20px;
  z-index: 1000;
  box-shadow: 2px 0 5px rgba(0,0,0,0.1);
}

.sidebar.open {
  transform: translateX(0);
}

.hamburger-btn {
  background-color: #d9cfc3;
  border: none;
  font-size: 1.5rem;
  color: #4a3f35;
  cursor: pointer;
  padding: 10px 15px;
  border-radius: 5px;
  margin-bottom: 20px;
}

.sidebar a {
  color: #4a3f35;
  text-decoration: none;
  font-weight: bold;
}

.sidebar a:hover {
  text-decoration: underline;
}
